.profileBlock{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 413px;
    height: 360px;
    margin: 120px auto;
    background-color: #FFFFFF;
    justify-content: space-around;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);
}

.imgAvatar{
    width: 96px;
    height: 96px;
}

.personalInfo{
    /*font-family: 'Montserrat';*/
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
}

.nameText{
    /*font-family: 'Montserrat';*/
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
}

.mailText{
    /*font-family: 'Montserrat';*/
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    opacity: 0.5;
}